import { defineStore } from "pinia";
import PerformerInfo from "@/interfaces/PerformerInfo";

interface User {
    user: {
        name: string,
        lastName: string,
        serName: string,
        phone: null | number,
        startDate: string,
    }
}

export interface EditUser {
    userName: string,
    userLastName: string,
    userSerName: string,
    userPhone: number | null,
}

export const useUserProfileStore = defineStore('userProfile',  {
    state: (): User  => ({
        user: {
            name: '',
            lastName: '',
            serName: '',
            phone: null,
            startDate: '',
        }     
    }),
    actions: {
        setUserInfo(user: PerformerInfo) {              
            this.user.name = user.userName;
            this.user.lastName = user.userLastName;
            this.user.serName = user.userSerName;
            this.user.phone = user.userPhone;            
            this.user.startDate = user.startDate;
        },        
        updateUserInfo(user: EditUser) {                    
            this.user.name = user.userName;
            this.user.lastName = user.userLastName;
            this.user.serName = user.userSerName;
            this.user.phone = user.userPhone;                
        }
    },    
})