import { defineStore } from "pinia";
import { UserRole } from "@/types/UserRoles";
import performerInfo from "../localDatabase/performerInfo.json";
import { useUserProfileStore } from "./profile";

interface UserStoreState {
    userRoleInfo: UserRole    
}

export const useUserStore = defineStore('userInfo',  {
    state: (): UserStoreState  => ({
        userRoleInfo: {
            userRole: null,
            textRole: 'none'
        },        
    }),
    getters: {

    },
    actions: {
        initUser(val: string | number | null) {                                             

            if(val === 0) {
                localStorage.setItem('userRole', String(0));

                this.userRoleInfo.userRole = 0;
                this.userRoleInfo.textRole = 'Заказчик'

                useUserProfileStore().setUserInfo(performerInfo)
                
                console.log("Пользователь заказчик", this.userRoleInfo)
            
            } else if(val === 1) {            
                localStorage.setItem('userRole', String(1));

                this.userRoleInfo.userRole = 1;
                this.userRoleInfo.textRole = 'Исполнитель'

                useUserProfileStore().setUserInfo(performerInfo)

                console.log("Пользователь исполнитель", this.userRoleInfo)

            } else if(val === 'null' || val === null) {
                localStorage.setItem('userRole', String(null));

                this.userRoleInfo.userRole = null;
                this.userRoleInfo.textRole = 'Гость'

                console.log('пользователь не зарегестрирован', this.userRoleInfo)
            }           
        },
        getUserInfoByPhone() {
            const userRoleFromLocalStorage = localStorage.getItem('userRole');
            if(userRoleFromLocalStorage !== 'null' && userRoleFromLocalStorage !== '1' && userRoleFromLocalStorage !== '0') {
                this.initUser(0) // вот тут меняем для дебага роли, на проде меняем на null
            }
            else if(userRoleFromLocalStorage === '1' || userRoleFromLocalStorage === '0') {
                this.initUser(Number(userRoleFromLocalStorage))    
            } else if (userRoleFromLocalStorage === 'null' || userRoleFromLocalStorage === null) {
                this.initUser(null)
            }
        },
        logOutOfProfile() {
            this.initUser(null);    
            window.location.reload();         
        }
    },
})